<template>
  <van-address-edit
    :areaList="areaList"
    :showPostal="true"
    :showSetDefault="true"
    :showSearchResult="true"
    :searchResult="searchResult"
    @save="onSave"
    @delete="onDelete"
    @change-detail="onChangeDetail"
  />
</template>

<style lang="less" scoped>
.van-address-edit{
  width: 100%;
}
</style>

<script>
  import "lib-flexible"
  import jarea from '../mock/area.json'
  export default {
    data() {
      return {
        title: "地址列表",
        radio: '1',
        footerData: "新增地址",
        areaList: jarea,
        searchResult: []
      }
    },
    methods: {
      onSave() {
        Toast('save');
      },
      onDelete() {
        Toast('delete');
      },
      onChangeDetail(val) {
        if (val) {
          this.searchResult = [{
            name: '黄龙万科中心',
            address: '杭州市西湖区'
          }];
        } else {
          this.searchResult = [];
        }
      }
    }
  }
</script>



